import React from 'react';
import { useAuth } from '../contexts/AuthContext';
import './ProtectedRoute.css';

interface ProtectedRouteProps {
  children: React.ReactNode;
}

const ProtectedRoute: React.FC<ProtectedRouteProps> = ({ children }) => {
  const { isAuthenticated, isLoading } = useAuth();
  const bypassAuth = process.env.REACT_APP_BYPASS_AUTH === 'true';

  // Nu ang loading, hin th loading trong main content (header vn hin th bn ngoi)
  // Ch hin th loading nu thc s ang kim tra (c token)
  if (isLoading) {
    return (
      <>
        <main className="app-main">
          <div className="loading">
            <div className="spinner"></div>
            <p>Checking access permissions...</p>
          </div>
        </main>
      </>
    );
  }

  // Nu cha ng nhp, hin th blank page (header vi GoogleAuth button vn hin th)
  if (!isAuthenticated && !bypassAuth) {
    return (
      <main className="app-main">
        <div className="blank-page" style={{ display: 'flex', alignItems: 'center', justifyContent: 'center', minHeight: '60vh' }}>
          <p style={{ color: '#6b7280', fontSize: '1rem' }}>Please log in to continue</p>
        </div>
      </main>
    );
  }

  //  ng nhp, hin th ni dung
  return <>{children}</>;
};

export default ProtectedRoute;
