import React, { createContext, useContext, useState, useEffect, ReactNode } from 'react';
import apiService from '../services/apiService';

interface User {
  email: string;
  name: string;
  picture: string;
  sub: string;
}

interface AuthContextType {
  user: User | null;
  isAuthenticated: boolean;
  isLoading: boolean;
  login: (user: User) => Promise<void>;
  logout: () => void;
  checkAuth: () => Promise<void>;
}

const AuthContext = createContext<AuthContextType | undefined>(undefined);

interface AuthProviderProps {
  children: ReactNode;
}

export const AuthProvider: React.FC<AuthProviderProps> = ({ children }) => {
  const bypassAuth = process.env.REACT_APP_BYPASS_AUTH === 'true';
  
  // Khi to state da trn vic c token hay khng  trnh loading khng cn thit
  // Kim tra ngay khi khi to component
  const getInitialToken = () => {
    if (typeof window === 'undefined') return null;
    return localStorage.getItem('auth_token');
  };
  
  const initialToken = getInitialToken();
  const shouldStartLoading = (initialToken !== null && initialToken !== '') || bypassAuth;
  
  const [user, setUser] = useState<User | null>(null);
  const [isAuthenticated, setIsAuthenticated] = useState(false);
  const [isLoading, setIsLoading] = useState(shouldStartLoading);
  
  const checkAuth = async () => {
    // Kim tra token trc - khng cn set loading nu khng c token
    const token = localStorage.getItem('auth_token');
    
    // Nu khng c token, set ngay lp tc m khng cn async
    if (!token || token.trim() === '') {
      if (!bypassAuth) {
        setUser(null);
        setIsAuthenticated(false);
        setIsLoading(false);
        return;
      }
    }

    try {
      setIsLoading(true);
      
      if (bypassAuth) {
        // Dev mode: bypass authentication
        setUser({ email: 'dev@example.com', name: 'Dev User', picture: '', sub: 'dev' });
        setIsAuthenticated(true);
        setIsLoading(false);
        return;
      }

      // C token, gi API  kim tra (token  c check  trn, khng th null)
      if (!token) {
        setIsLoading(false);
        return;
      }
      
      const response = await apiService.checkAuth();
      if (response.isAuthenticated) {
        // Nu c user t response, dng user 
        if (response.user) {
          setUser(response.user);
          setIsAuthenticated(true);
        } else {
          // Nu khng c user t response nhng isAuthenticated = true
          // Th decode token t localStorage  ly user info
          try {
            const payload = JSON.parse(atob(token.split('.')[1]));
            const userFromToken: User = {
              email: payload.email,
              name: payload.name,
              picture: payload.picture,
              sub: payload.sub
            };
            setUser(userFromToken);
            setIsAuthenticated(true);
          } catch (decodeError) {
            // Nu khng decode c token, clear v yu cu ng nhp li
            localStorage.removeItem('auth_token');
            setUser(null);
            setIsAuthenticated(false);
          }
        }
      } else {
        // Response tr v isAuthenticated = false, clear token
        localStorage.removeItem('auth_token');
        setUser(null);
        setIsAuthenticated(false);
      }
    } catch (error) {
      // Nu c li (401, network error, etc.), clear auth v yu cu ng nhp li
      localStorage.removeItem('auth_token');
      setUser(null);
      setIsAuthenticated(false);
    } finally {
      setIsLoading(false);
    }
  };

  const login = async (userData: User) => {
    try {
      console.log('Attempting login for:', userData.email);
      const response = await apiService.login(userData);
      console.log('Login response:', response);
      
      if (response.success) {
        console.log('Login successful, setting user:', userData.email);
        setUser(userData);
        setIsAuthenticated(true);
        // No reload needed - keep the same URL
      } else {
        console.error('Login failed - response not successful:', response);
        throw new Error(response.message || 'Login failed');
      }
    } catch (error) {
      console.error('Login error:', error);
      console.error('Error details:', {
        message: error instanceof Error ? error.message : String(error),
        userData: { email: userData.email, name: userData.name }
      });
      
      // Show user-friendly error message
      if (
        typeof error === 'object' &&
        error !== null &&
        'message' in error &&
        typeof (error as any).message === 'string' &&
        (error as any).message.includes('mangoforsalon.com')
      ) {
        alert('Only emails with @mangoforsalon.com domain are allowed to access the system.');
      } else {
        const errorMessage = error instanceof Error ? error.message : 'Unknown error';
        alert(`Login failed: ${errorMessage}. Please try again.`);
      }
      throw error;
    }
  };

  const logout = async () => {
    try {
      await apiService.logout();
    } catch (error) {
      // Ignore logout errors
    } finally {
      // Clear token from localStorage
      localStorage.removeItem('auth_token');
      setUser(null);
      setIsAuthenticated(false);
    }
  };

  useEffect(() => {
    // Ch gi checkAuth nu c token hoc bypassAuth
    // Nu khng c token, khng cn gi checkAuth v  set state ng t u
    const token = localStorage.getItem('auth_token');
    if (token && token.trim() !== '') {
      checkAuth();
    } else if (bypassAuth) {
      checkAuth();
    }
    // Nu khng c token v khng bypass, state  c set ng t u (isLoading = false)
  }, []);

  const value: AuthContextType = {
    user,
    isAuthenticated,
    isLoading,
    login,
    logout,
    checkAuth
  };

  return (
    <AuthContext.Provider value={value}>
      {children}
    </AuthContext.Provider>
  );
};

export const useAuth = (): AuthContextType => {
  const context = useContext(AuthContext);
  if (context === undefined) {
    throw new Error('useAuth must be used within an AuthProvider');
  }
  return context;
};
